// MessageItem.js
import React, { useEffect, useRef } from 'react';
import {
  Avatar,
  ListItem,
  ListItemAvatar,
  ListItemText,
  Typography,
  IconButton,
} from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';  // 削除アイコンをインポート
import { makeStyles } from '@material-ui/core/styles';

import { gravatarPath } from '../gravatar';
import { deleteMessage } from '../firebase';  // firebase.jsからdeleteMessageをインポート

const useStyles = makeStyles(() => ({
  inline: {
    display: 'inline',
  },
}));

const MessageItem = ({ isLastItem, name, text, id }) => {  // idプロパティを追加
  const ref = useRef(null);
  const classes = useStyles();
  const avatarPath = gravatarPath(name);

  const handleDelete = () => {  // 削除ハンドラーを追加
    deleteMessage(id);
  };

  useEffect(() => {
    if (isLastItem) {
      ref.current.scrollIntoView({ behavior: 'smooth' });
    }
  }, [isLastItem]);

  return (
    <ListItem divider={true} ref={ref}>
      <ListItemAvatar>
        <Avatar src={avatarPath} />
      </ListItemAvatar>
      <ListItemText
        primary={name}
        secondary={
          <Typography
            component="span"
            variant="body2"
            className={classes.inline}
            color="textPrimary"
          >
            {text}
          </Typography>
        }
      />
      <IconButton edge="end" onClick={handleDelete}>
        <DeleteIcon />
      </IconButton>
    </ListItem>
  );
};

export default MessageItem;
