import React, { useState } from 'react';
import ReactDOM from 'react-dom';

import './index.css';
import App from './components/App';
import PasswordPrompt from './components/PasswordPrompt';

const correctPassword = 'mil5';  // 適切なパスワードを設定してください

const Main = () => {
  const [passwordEntered, setPasswordEntered] = useState(false);

  if (!passwordEntered) {
    return <PasswordPrompt correctPassword={correctPassword} onSuccess={() => setPasswordEntered(true)} />;
  }

  return <App />;
};

ReactDOM.render(
  <React.StrictMode>
    <Main />
  </React.StrictMode>,
  document.getElementById('root')
);
